.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.algo-viz-content {
  height: 100vh;
  background-color: #6e9196;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-around;
}

.algo-viz-content-unsorted {
  height: 100vh;
  background-color: #6e9196;
  display: flex;
  flex: 1 1 50%;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
  border-right: 1px solid black;
  overflow-x: auto;
}

.algo-viz-content-sorted {
  height: 100vh;
  background-color: #6e9196;
  display: flex;
  flex: 1 1 50%;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  overflow-x: auto;
}

.App-link {
  color: #09d3ac;
}

@media only screen and (max-width: 480px) {
  .algo-viz-content {
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 100vw;
  }

  .algo-viz-content-unsorted {
    width: 100vw;
    border-right: none;
    border-bottom: 1px solid black;
  }
}